.App {
    text-align: center;
    background: rgba(16, 6, 147, 0.11);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    height: 100vh;
}

.bodyContainer {
    padding: 30px;
    background: #fff;
    border-radius: 7px;
    width: 70%;
}

.rbc-toolbar-label {
    color: #5388e4;
    font-weight: bold;
    font-size: 25px;
    margin-bottom: 10px;
}

.rbc-btn-group {
    margin-bottom: 0 !important;
}

.rbc-btn-group>button {
    padding: 8px 15px !important;
}

h1,
.instruction {
    font-family: fantasy;
    text-align: left;
}

.instruction {
    font-family: sans-serif;
    font-size: 30px;
}

.bodyContainer__title {
    height: 60px;
}

.modal {
    display: block;
}

.rbc-toolbar span:first-child button:first-child {
    background-color: #fff;
    color: black;
    font-weight: 500;
    text-transform: capitalize;
    border-radius: 4px;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
}

.rbc-toolbar span:first-child button:first-child:hover {
    color: #5388e4;
}

.rbc-toolbar span:first-child button:nth-child(2) {
    text-indent: -9999px;
    line-height: 0;
    margin-left: 5px;
    border-radius: 4px;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
}

.rbc-toolbar span:first-child button:nth-child(2):hover {
    background: #5388e4;
}

.rbc-toolbar span:first-child button:nth-child(2)::after {
    content: '◀';
    text-indent: 0;
    display: block;
    line-height: initial;
    font-size: 13px;
}

.rbc-toolbar span:first-child button:nth-child(3) {
    text-indent: -9999px;
    line-height: 0;
    border-radius: 4px;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
}

.rbc-toolbar span:first-child button:nth-child(3):hover {
    background: #5388e4;
}

.rbc-toolbar span:first-child button:nth-child(3)::after {
    content: '▶';
    text-indent: 0;
    display: block;
    line-height: initial;
    font-size: 13px;
}

.rbc-active {
    background-color: #5388e4 !important;
    color: white !important;
}

.rbc-header {
    background-color: #5388e4;
    color: white !important;
    padding-top: 13px !important;
    padding-bottom: 13px !important;
    text-transform: uppercase;
}

.rbc-toolbar {
    -ms-flex-wrap: wrap-reverse;
        flex-wrap: wrap-reverse;
}

.rbc-month-view {
    border-radius: 10px;
}

.rbc-today {
    background-color: lightgray !important;
}

.rbc-event-content {
    background: #5388e4;
}

.rbc-btn-group {
    white-space: pre-wrap !important;
    margin-bottom: 10px;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@media screen and (min-width: 1080px) {
    .bodyContainer {
        width: 70%;
        margin-left: auto;
        margin-right: auto;
    }
}

@media screen and (max-width: 500px) {
    .bodyContainer {
        margin: 2%;
    }
}


